import request from '@/request/api'
/**===========基本信息接口 */

// 数据接入申请-获取列表
export function getListApi(data = {}) {
    return request({
        url: '/workorder/DataccessappAction/queryList.htm',
        method: 'post',
        data: data
    })
}


//数据接入申请-详情
export function getDetailsApi(id = "") {
    return request({
        url: '/workorder/DataccessappAction/detail.htm',
        method: 'post',
        params:{ id: id }
    })
}


// 删除操作
export function deleteDataaccess(id = "") {
    return request({
        url: '/workorder/DataccessappAction/delete.htm',
        method: 'post',
        params: { id: id }
    })
}

//数据接入申请-添加
export function addDataaccess(data = {}) {
    return request({
        url: '/workorder/DataccessappAction/addDataccessapp.htm',
        method: 'post',
        data: data
    })
}

//数据接入申请-审核前保存表单（保存草稿）
export function updateDataccess(data = {}) {
    return request({
        url: '/workorder/DataccessappAction/updateDataccessapp.htm',
        method: 'post',
        data: data
    })
}

//数据接入申请-审核结束后评分
export function getPlayScore(data = {}) {
    return request({
        url: '/workorder/DataccessappAction/playScore.htm',
        method: 'post',
        data: data
    })
}

